@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}

.article-page {
	.article-title {
		max-width: 820px;
		h1 {
			font-weight: 600;
			font-size: 36px;
			color: var(--text-color);
			@media (min-width: 1000px) {
				font-size: 55px;
			}
		}
	}
	.article-info {
		max-width: 820px;
		color: var(--subtext-color);
		font-size: 13px;
		line-height: 1.4em;
	}
	.article-content {
		margin-bottom: 50px;
		padding-bottom: 30px;
		border-bottom: 1px solid var(--miscellaneous-color);
		.fs-16 {
			font-size: 16px;
			line-height: 1.6em;
		}
		.fs-17 {
			font-size: 17px;
			line-height: 1.7em;
		}
		.fs-18 {
			font-size: 18px;
			line-height: 1.8em;
			font-weight: 500;
		}
		h2, h3, h4, h5, h6 {
			max-width: 820px;
			margin: 10px auto;
			a {
				color: var(--text-color);
			}
		}
		table {
			margin-top: 20px;
			margin-bottom: 20px;
		}
		p {
			margin-left: auto;
			margin-right: auto;
			max-width: 820px;
			line-height: 1.55em;
			display: block;
		}
		img {
			width: 100%;
		}
		.flex {
			align-items: center;
		}
	}
	.share-social {
		max-width: 820px;
		margin: 50px auto 60px;
		display: flex;
	}
	.social-sharing {
		display: none;
		flex-wrap: wrap;
		margin: 0 -4px;
		transition: 0.2s all ease-in-out;
		&.active {
			display: flex;
		}
		>li {
			padding: 0 4px;
			&:last-child {
				@include margin-end(8px);
			}
		}
		a {
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 32px;
			height: 32px;
			border-radius: var(--border-radius);
			svg {
				width: 12px;
				height: 12px;
				path {
					fill: #fff;
				}
			}
		}
	}
	.share-facebook {
		background: #4267b2;
	}
	.share-twitter {
		background: #55acee;
	}
	.share-pinterest {
		background: #cb2027;
	}
	.share-list-btn {
		padding: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 32px;
		height: 32px;
		background: #95d03a;
		border-radius: var(--border-radius);
		cursor: pointer;
		svg {
			width: 12px;
			height: 12px;
			path {
				fill: #fff;
			}
		}
	}
	.section-container {
		>.h4 {
			font-size: 30px;
			font-weight: 500;
			margin-bottom: 30px;
		}
	}
	.list-comments {
		margin-bottom: 35px;
	}
	.comment-item {
		background: #f5f5f5;
		border: 1px solid var(--miscellaneous-color);
		padding: 20px;
		margin-bottom: 20px;
		border-radius: var(--border-radius);
	}
	.comment-inner {
		margin: 0;
		>div {
			padding: 0;
		}
	}
	.comment-author {
		flex: 0 0 32px;
		max-width: 32px;
		@include margin-end(15px);
	}
	.comment-info {
		margin-bottom: 10px;
		.comment-author {
			@include margin-end(5px);
			font-size: 18px;
			font-weight: 500;
		}
	}
	.comment-create-at {
		color: var(--subtext-color);
	}
	.comment-content {
		line-height: 1.55em;
		p {
			margin: 0;
		}
	}
}
.article-comment-form-wrapper {
	margin-bottom: 60px;
	margin-top: 50px;
	h2 {
		font-size: 30px;
		margin-bottom: 30px;
		font-weight: 500;
	}
	.form-status {
		font-size: 15px;
		margin-bottom: 20px;
	}
	input:not(.btn), textarea {
		background: #f5f5f5;
		border: 1px solid var(--miscellaneous-color);
		border-radius: var(--border-radius);
		font-size: 15px;
	}
	input.btn {
		max-width: 200px;
	}
	.form-warning {
		color: var(--primary-color);
	}
	.form-field {
		margin-bottom: 20px;
	}
	.form__message {
		font-size: 15px;
		font-weight: 400;
		color: #fd1414;
		margin-top: 10px;
		display: block;
	}
}